import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import axios from "axios";
import FormKodeBarang from "./Component/FormKodeBarang";
import { connect } from "react-redux";
import { editRosok } from "../actions/rosokAction";
import notrespond from "../Images/500.svg";
import activelogo from "../Images/active.png";
import nonactive from "../Images/nonactive.png";
import Swal from "sweetalert2";
import { Link } from "react-router-dom";
import FormKodeBarangEdit from "./Component/FormKodeBarangEdit";
import { getListBarang } from "../actions/kodeBarangAction";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const server = process.env.REACT_APP_BACKEND_URL;
function status(cell, row) {
  if (row.statusAktif === "AKTIF") {
    return (
      <div>
        <img src={activelogo} style={{ width: 50, height: 50 }} alt="ACTIVE" />
      </div>
    );
  } else {
    return (
      <div>
        <img
          src={nonactive}
          alt="NON ACTIVE"
          style={{ width: 50, height: 50 }}
        />
      </div>
    );
  }
}
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "subcategory", {
          data: {
            kodeJenis: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Hapus Data Gagal",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .then(() => dispatch(getListBarang()));
    }
  });
}
const maptoState = (state) => {
  return {
    KodeBarangList: state.provinsi.kodeBarang,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};
class KodeBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      KodeBarang: [],
      response: "",
      display: "none",
      columns: [
        {
          dataField: "kodeKategori",
          text: "Kode Kategori",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kodeJenis",
          text: "Kode Jenis",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarCetak",
          text: "Kadar Cetak",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarBeli",
          text: "Kadar Beli",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarJual",
          text: "Kadar Jual",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "namaJenis",
          text: "Nama Jenis",
        },
        {
          dataField: "statusAktif",
          text: "Status",
          sort: true,
          formatter: status,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeJenis)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  data-toggle="modal"
                  data-target="#editModal"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editRosok(
                        row.kodeJenis,
                        row.statusAktif,
                        row.kodeKategori,
                        row.namaJenis,
                        row.kadarBeli,
                        row.kadarJual,
                        row.kadarCetak
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }

  handleSubit(data) {
    data = {
      kodeKategori: data.kodeKategori,
      kodeKelompok: "PERHIASAN",
      kodeJenis: data.kodeJenis,
      statusAktif: data.statusAktif ? data.statusAktif : "AKTIF",
      kadarCetak: data.kadarCetak,
      kadarBeli: data.kadarBeli,
      namaJenis: data.namaJenis,
      kadarJual: data.kadarJual,
    };
    console.log(data);
    axios
      .post(server + "subcategory", data)
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Ditambahkan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Kirim Data Error",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getListBarang()))
      .then(() => this.props.dispatch(reset("KodeBarang")));
  }
  handleEdit(data) {
    axios
      .put(server + "subcategory", {
        kodeKategori: data.kodeKategori,
        kodeKelompok: "PERHIASAN",
        kodeJenis: data.kodeJenis,
        statusAktif: data.statusAktif ? data.statusAktif : "AKTIF",
        kadarCetak: data.kadarCetak,
        kadarBeli: data.kadarBeli,
        namaJenis: data.namaJenis,
        kadarJual: data.kadarJual,
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Ditambahkan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal1").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Kirim Data Error",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getListBarang()));
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <ol className="breadcrumb mb-2">
          <Link to="KlasifikasiBarang">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h3 className="card-title mb-4" style={{ fontSize: 35 }}>
                  Data Kode Barang
                </h3>
                <form action="#">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          type="button"
                          className="btn btn-info"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          Tambah Data Kode Barang
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                {this.props.KodeBarangList ? (
                  <ToolkitProvider
                    keyField="id"
                    data={this.props.KodeBarangList}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <div className="text-right mt-5 mb-3">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Pencarian"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          pagination={paginationFactory()}
                        />
                        <ExportCSVButton
                          {...props.csvProps}
                          className="btn btn-primary"
                        >
                          Export CSV
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img
                          src={notrespond}
                          style={{ width: "30%" }}
                          alt="NOT RESPOND"
                        />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <Skeleton width={1100} height={180} />
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Kode Barang
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormKodeBarang onSubmit={(data) => this.handleSubit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Kode Barang
                </h4>
                <button
                  type="button"
                  id="dismiss_modal1"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormKodeBarangEdit
                  onSubmit={(data) => this.handleEdit(data)}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(KodeBarang);
