import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import axios from "axios";
import Swal from "sweetalert2";
import validationRosok from "../../Validations/validationRosok";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const maptoState = (state) => {
  return {
    initialValues: {
      kodeJenis: state.provinsi.editRosok.kodeJenis,
      statusAktif: state.provinsi.editRosok.statusAktif,
      kodeKategori: state.provinsi.editRosok.kodeKategori,
      namaJenis: state.provinsi.editRosok.namaJenis,
      kadarBeli: state.provinsi.editRosok.kadarBeli,
      kadarJual: state.provinsi.editRosok.kadarJual,
      kadarCetak: state.provinsi.editRosok.kadarCetak,
    },
  };
};

const server = process.env.REACT_APP_BACKEND_URL;
class FormKodeBarangEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      listPerhiasan: [],
      listJual: [],
      listBeli: [],
      listKategori: [],
    };
  }

  componentDidMount() {
    axios
      .get(server + "rates?tipeKadar=KADAR-CETAK")
      .then((res) => {
        this.setState({
          listPerhiasan: res.data,
        });
      })
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Ambil Data Kadar Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
    axios
      .get(server + "rates?tipeKadar=KADAR-BELI")
      .then((res) => {
        this.setState({
          listBeli: res.data,
        });
      })
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Ambil Data Kadar Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
    axios
      .get(server + "rates?tipeKadar=KADAR-JUAL")
      .then((res) => {
        this.setState({
          listJual: res.data,
        });
      })
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Ambil Data Kadar Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
    axios
      .get(server + "categorys")
      .then((res) => {
        this.setState({
          listKategori: res.data,
        });
      })
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Ambil Data Kategori Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          {/* Awal FIeld */}
          <div className="col-md-3">
            <div className="form-group">
              <label htmlFor="" className="text-black">
                Kode Kategory
              </label>
              <Field
                name="kodeKategori"
                component="select"
                className="form-control"
                value="DEFAULT"
                readOnly={true}
              >
                {this.state.listKategori.map((list) => (
                  <option value={list.kodeKategori} key={list.kodeKategori}>
                    {list.kodeKategori}
                  </option>
                ))}
              </Field>
            </div>
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-3">
            <Field
              name="kodeJenis"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Jenis"
              readOnly={true}
            />
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-2">
            <div className="form-group">
              <label htmlFor="" className="text-black">
                Kadar Cetak
              </label>
              <Field
                name="kadarCetak"
                component="select"
                className="form-control"
              >
                {this.state.listPerhiasan.map((list) => (
                  <option value={list.kadar} key={list.kadar}>
                    {list.kadar}
                  </option>
                ))}
              </Field>
            </div>
          </div>
          {/* AKhir FIeld */}
          {/* Awal Field */}
          <div className="col-md-2">
            <div className="form-group">
              <label htmlFor="" className="text-black">
                Kadar Jual
              </label>
              <Field
                name="kadarJual"
                component="select"
                className="form-control"
              >
                {this.state.listJual.map((list) => (
                  <option value={list.kadar} key={list.kadar}>
                    {list.kadar}
                  </option>
                ))}
              </Field>
            </div>
          </div>
          {/* AKhir FIeld */}
          {/* Awal Field */}
          <div className="col-md-2">
            <div className="form-group">
              <label htmlFor="" className="text-black">
                Kadar Beli
              </label>
              <Field
                name="kadarBeli"
                component="select"
                className="form-control"
              >
                {this.state.listBeli.map((list) => (
                  <option value={list.kadar} key={list.kadar}>
                    {list.kadar}
                  </option>
                ))}
              </Field>
            </div>
          </div>
          {/* AKhir FIeld */}
          {/* Awal Field */}
          <div className="col-md-6">
            <Field
              name="namaJenis"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Nama Jenis"
            />
          </div>
          {/* Akhir Field */}
          <div className="col-lg-12 col-md-12 mt-3">
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info"
                disabled={this.props.submitting}
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKodeBarangEdit = reduxForm({
  form: "KodeBarangEdit",
  enableReinitialize: true,
  validate: validationRosok,
})(FormKodeBarangEdit);
export default connect(maptoState, null)(FormKodeBarangEdit);
